<template>
    <div class="wrap">
        <div class="banner">
            <div class="title">克复中原&nbsp;&nbsp;&nbsp;鞠躬尽瘁</div>
            <div class="sub-title">诸葛亮蜀汉复兴的一生</div>
            <div class="sub-text">
                <span class="border-r">难度&nbsp;&nbsp;&nbsp;&nbsp;初级</span>
                <span class="border-r">学习人数&nbsp;&nbsp;2143</span>
                <span>课程评分&nbsp;&nbsp;4.5分</span>
            </div>
        </div>
        <div class="resource-container">
            <div class="header-box">
                <div class="img-box">
                    <img src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1539080377947&di=cf913e5df58b1623cb52ad2399b47e60&imgtype=0&src=http%3A%2F%2Fimg4.dwstatic.com%2Fnews%2F1509%2F305031447914%2F305031447927.jpg" :draggable="false">
                </div>
                <div class="content-box">
                    <div class="heade">
                        <span class="title">
                            鞠躬尽瘁-诸葛亮蜀汉复兴的一生
                        </span>
                        <span class="rate">
                            <span class="text">课程评价&nbsp;&nbsp;</span>
                            <el-rate class="inline" v-model="rate" disabled show-score text-color="#ff9900" score-template="{value}">
                            </el-rate>
                        </span>
                    </div>
                    <div class="text">诸葛亮，字孔明，生卒于公元181年—公元234年，出生于山东临沂市沂南县，是三国时期蜀汉国的丞相。诸葛亮是历史上着名的政治家、军事家、发明家。诸葛亮的一生被人们传颂为“鞠躬尽瘁，死而后已”。他也是是中国传统文化中忠臣与智者的代表人物。接下来就由小编来揭秘下历史上的诸葛亮。</div>
                    <div class="btns">
                        <span class="collect">
                            <i class="iconfont icon-star"></i>
                            &nbsp;收藏课程
                        </span>
                        <span class="baoming">
                            <i class="iconfont icon-kecheng"></i>
                            &nbsp;课程报名
                        </span>
                    </div>
                </div>
            </div>
            <div class="detail-box">
                <div class="left"></div>
                <div class="right"></div>
            </div>
        </div>
    </div>
</template>
<script>

export default {
    data() {
        return {
            rate: 4.5
        };
    }
};
</script>
<style lang="less" scoped>
.banner {
    width: 100%;
    height: 360px;
    color: #fff;
    background-image: url("../../../static/jpkc.png");
    background-size: 100%;
    background-repeat: no-repeat;
    padding-top: 95px;
    .title {
        font-size: 3.6rem;
        text-shadow: 0 5px 6px #2857c4;
    }
    .sub-title {
        margin-top: 30px;
        font-size: 1.52rem;
        letter-spacing: 12.5px;
    }
    .sub-text {
        width: 400px;
        margin: 0 auto;
        margin-top: 66px;
        color: #c9d2e9;
        span {
            width: 33.33%;
            float: left;
        }
        .border-r {
            border-right: 2px solid #c8cdd4;
        }
    }
}
.resource-container {
    width: 1200px;
    margin: 0 auto;
    .header-box {
        width: 100%;
        height: 280px;
        margin-top: 42px;
        .img-box {
            width: 460px;
            height: 100%;
            float: left;
            img {
                width: 100%;
                height: 100%;
            }
        }
        .content-box {
            width: 740px;
            height: 100%;
            float: left;
            background: #e8e8e8;
            padding: 0 20px;
            text-align: left;
            .heade {
                width: 100%;
                height: 66px;
                line-height: 66px;
                border-bottom: 1px solid #aaa;
                .title {
                    font-size: 1.4rem;
                }
                .rate {
                    float: right;
                    color: #aaa;
                    font-size: 0.9rem;
                    .text {
                        vertical-align: middle;
                    }
                    .inline {
                        vertical-align: middle;
                        display: inline-block;
                    }
                }
            }
            .text {
                width: 100%;
                height: 158px;
                padding-top: 8px;
                font-size: 0.9rem;
                color: #666;
            }
            .btns {
                span {
                    padding: 8px 15px;
                    color: #fff;
                }
                .collect {
                    background: #63a9ff;
                }
                .baoming {
                    background: #29d4ae;
                    margin-left: 18px;
                }
            }
        }
    }
    .detail-box {
        margin-top: 32px;
        .left{
            width: 900px;
            float: left;
            border: 1px solid #aaa;
            background: #fff;
        }
        .right{
            width: 280px;
            float: right;
        }
    }
}
</style>



